<template>
  <section class="category-group">
    <img
      class="demo-bg"
      src="@/assets/images/site/category-background.jpeg"
      alt=""
    />
    <!--    category navbar  BEGINS-->
    <category-nav></category-nav>
    <!--      <category-book-list :books="books"> </category-book-list>-->
    <div class="book-list-container">
      <template v-for="book in $store.state.selectedCategoryBooks">
        <div :key="book.book_id" class="book-container">
          <img
            :src="require(`@/assets/images/books/` + bookImageFileName(book))"
            :alt="book.name"
          />
          <div class="book-desc" id="book.book_id">
            <router-link class="title" to="#">{{ book.title }}</router-link>
            <span class="by">by</span>
            <span class="author">{{ book.author }}</span>
            <span class="price">${{ (book.price / 100).toFixed(2) }}</span>
            <div class="action-group">
              <button class="read-now" v-if="book.isPublic">Read Now</button>
              <button class="add-to-cart" @click="addToCart(book)">
                Add to Cart
              </button>
            </div>
          </div>
        </div>
      </template>
    </div>
  </section>
</template>

<script>
import ApiService from "@/services/ApiService";
import CategoryNav from "@/components/CategoryNav";
// import CategoryBookList from "@/components/CategoryBookList";

export default {
  name: "category",
  components: {
    CategoryNav,
    // CategoryBookList,
  },
  data: function () {
    return {
      // books: [],
    };
  },
  created: function () {
    const self = this;
    this.$store.dispatch("selectCategory", this.$route.params.name);
    this.$store.dispatch("fetchSelectedCategoryBooks").catch((err) => {
      console.log("hi I catch you" + err);
      self.$router.push("/404");
    });
  },
  methods: {
    addToCart(book) {
      this.$store.dispatch("addToCart", book);
      //this.$store.state.cart.addItem(book, 1);//bad
    },
    fetchSelectedCategoryBooks() {
      const vm = this;
      ApiService.fetchSelectedCategoryBooks(this.$route.params.name)
        .then((data) => {
          console.log("Books is ~~~:" + data);
          vm.books = data;
        })
        .catch((reason) => {
          console.log("Error: " + reason);
        });
    },
    bookImageFileName: function (book) {
      let name = book.title;
      name = name.replace(":", "-");
      return `${name}.jpeg`;
    },
  },
};
</script>

