import Vue from "vue";
import Vuex from "vuex";
import ApiService from "@/services/ApiService";
import { ShoppingCart } from "@/models/ShoppingCart";

Vue.use(Vuex);
export const CART_STORAGE_KEY = "cart";
export const ORDER_DETAIL_STORAGE_KEY = "orderDetails";
export default new Vuex.Store({
  state: {
    surcharge: 500,
    categories: [], // shared with CategoryNav, HeaderDropdown
    selectedCategoryName: "",
    selectedCategoryBooks: [],
    cart: new ShoppingCart(),
    orderDetails: null,
  },
  mutations: {
    //sync,can be tracked by Vue dev tool
    SET_CATEGORIES(state, newCategories) {
      state.categories = newCategories;
    },
    SELECT_CATEGORY(state, categoryName) {
      state.selectedCategoryName = categoryName;
    },
    SET_SELECTED_CATEGORY_BOOKS(state, categoryBooks) {
      state.selectedCategoryBooks = categoryBooks;
    },
    SET_CART(state, shoppingCart) {
      localStorage.setItem(CART_STORAGE_KEY, JSON.stringify(shoppingCart));
      let newCart = new ShoppingCart();
      shoppingCart.items.forEach((item) => {
        newCart.addItem(item.book, item.quantity);
      });
      state.cart = newCart;
    },
    ADD_TO_CART(state, book) {
      state.cart.addItem(book, 1);
      localStorage.setItem(CART_STORAGE_KEY, JSON.stringify(this.state.cart));
    },
    UPDATE_CART(state, { book, quantity }) {
      state.cart.update(book, quantity);
      localStorage.setItem(CART_STORAGE_KEY, JSON.stringify(this.state.cart));
    },
    CLEAR_CART(state) {
      state.cart.clear();
      localStorage.setItem(CART_STORAGE_KEY, JSON.stringify(this.state.cart));
    },
    //session storage
    SET_ORDER_DETAILS(state, orderDetails) {
      state.orderDetails = orderDetails;
      sessionStorage.setItem(
        ORDER_DETAIL_STORAGE_KEY,
        JSON.stringify(orderDetails)
      );
    },
    CLEAR_ORDER_DETAILS(state) {
      sessionStorage.removeItem(ORDER_DETAIL_STORAGE_KEY);
      state.orderDetails = null;
    },
  },
  actions: {
    //async
    fetchCategories(context) {
      ApiService.fetchCategories()
        .then((categories) => {
          console.log("Data is: ", categories);
          context.commit("SET_CATEGORIES", categories);
        })
        .catch((reason) => {
          console.log("Error: " + reason);
        });
    },
    selectCategory(context, name) {
      context.commit("SELECT_CATEGORY", name);
    },
    fetchSelectedCategoryBooks(context) {
      return ApiService.fetchSelectedCategoryBooks(
        context.state.selectedCategoryName
      )
        .then((categoryBooks) => {
          console.log("Books by selected category name are: ", categoryBooks);
          context.commit("SET_SELECTED_CATEGORY_BOOKS", categoryBooks);
        })
        .catch((reason) => {
          console.log("Error: " + reason);
          throw reason;
        });
    },
    addToCart(context, book) {
      context.commit("ADD_TO_CART", book);
    },
    updateCart(context, { book, quantity }) {
      context.commit("UPDATE_CART", { book, quantity });
    },
    clearCart(context) {
      context.commit("CLEAR_CART");
    },
    placeOrder(context, customerForm) {
      context.commit("CLEAR_ORDER_DETAILS");
      return ApiService.placeOrder({
        cart: context.state.cart,
        customerForm: customerForm,
      }).then((orderDetails) => {
        context.commit("CLEAR_CART");
        context.commit("SET_ORDER_DETAILS", orderDetails);
      });
      //if promise from placeOrder service is rejected, do nothing, error will propagate up to calling object
    },
  },
  getters: {
    surcharge(state) {
      return state.surcharge;
    },

    defaultCategoryName() {
      return "Default Book Category Name";
    },
    categoryName(state, getters) {
      return state.selectedCategoryName || getters.defaultCategoryName;
    },
  },
});
